<template>
  <div class="container">
    <h1>Concours</h1>

    <div class="search" style="position: absolute; top: 200px">
      <input type="text" v-model="searchQuery" placeholder="Rechercher un concours" >
    </div>
    <div class="line"/>
    <div class="cardContainer">
      <div v-for= "concours in filteredConcours" v-bind:key="concours.id" >
        <ConcoursCard :concours=concours />
      </div>
    </div>
  </div>
</template>

<script>
import ConcoursCard from "@/components/Concours/ConcoursCard.vue";
import informations from "/src/Python/venv/Scripts/db/concours.json";

export default {
  name: "ConcoursView",
  components: {ConcoursCard},
  data() {
    return {
      informations,
      searchQuery: ''
    };
  },
  computed: {
    filteredConcours() {
      if (this.searchQuery) {
        const query = this.searchQuery.toLowerCase();
        return Object.values(this.informations.concours).filter((concours) => {
          return concours.caption.toLowerCase().includes(query);
        })
      } else {
        return this.informations.concours;
      }
    }
  }
}
</script>

<style scoped>
.cardContainer {
  position: relative;
  height: 300px;
  width: 80%;
  top : 200px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  transform: translate(-50%, 50%);
  left: 50%;
}
.container {
  display: flex ;
  flex-direction: column;
  height: 100vh;
}

.search {
  position: relative;
  top: 200px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.search input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 1.2rem;
  padding-left: 10px;
  border-radius: 10px;
  text-align: center;
}

.line {
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30%;
  height: 1px;
  background-color: lightgrey;
}

</style>